<template>
  <div class="select-view">
    <div class="section">
      <h3 class="section-title"></h3>
      <Select
        :option="option"
        title=""
        v-model="select"/>
      <span v-if="select">{{select}}</span>
    </div>
    <div class="section">
      <h3 class="section-title"></h3>
      <Select
        :option="option"
        title="2"
        :bolck="true"
        :size="18"
        color="#2185D0"
        v-model="select2"
        :selectedIndex="[1]"/>
      <span v-if="select2">{{select2}}</span>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import Select from '@/components/Select.vue'

let option = [
  {
    text: '',
    value: ''
  },
  {
    text: '',
    value: ''
  },
  {
    text: '',
    value: ''
  },
  {
    text: '',
    value: ''
  },
  {
    text: '',
    value: ''
  }
]

export default {
  data () {
    return {
      select: '',
      select2: '',
      option: option
    }
  },
  components: {
    Select
  }
}
</script>

<style scoped lang="less">
@import "../assets/less/ProPreboot.less";
.select-view{
  width:100%;
  height:100%;
  padding:15px;
}
.section{
  margin-bottom:20px;
  .section-title{
    font-size:16px;
    color:@gray-dark;
    font-weight:normal;
    margin-bottom:0.5em;
  }
}
</style>



