<template>
  <Page>
    <div class="section">
      <h3 class="section-title">icon</h3>
      <Loading/>
      <Loading icon="loading1"/>
      <Loading icon="loading2"/>
      <Loading icon="loading3"/>
      <Loading icon="loading4"/>
    </div>
    <div class="section">
      <h3 class="section-title"></h3>
      <Loading/>
      <Loading :size="16" icon="loading1"/>
      <Loading :size="18" icon="loading2"/>
      <Loading :size="20" icon="loading3"/>
      <Loading :size="22" icon="loading4"/>
    </div>
    <div class="section">
      <h3 class="section-title"></h3>
      <Loading text="" :size="18" :text-size="18" color="#2185D0" text-color="#2185D0"/>
    </div>
    <div class="section">
      <h3 class="section-title"></h3>
      <div class="loading-parent">
        <Loading role="block" :size="20"/>
        <p></p>
      </div>
      <div class="loading-parent">
        <Loading role="block" :size="20" background="rgba(0,0,0,0.5)" color="#fff"/>
        <p></p>
      </div>
      <div class="loading-parent">
        <Loading role="block" :size="20" text=""/>
        <p></p>
      </div>
      <div class="loading-parent">
        <Loading role="block" :size="20" text="" direction="column"/>
        <p></p>
      </div>
    </div>
    <div class="section">
      <h3 class="section-title"> </h3>
      <p>rolefullscreen</p>
      <!-- <Loading role="fullscreen" text="" direction="column" :size="20" :text-size="12"/> -->
    </div>
  </Page>
</template>

<script>
import Page from '@/components/Page.vue'
import Loading from '@/components/Loading.vue'

export default {
  components: {
    Page,
    Loading
  }
}
</script>
<style lang='less' scoped>
@import '../assets/less/ProPreboot.less';
.section{
  margin-bottom:20px;
  .section-title{
    font-size:16px;
    color:@gray-dark;
    font-weight:normal;
    margin-bottom:0.5em;
  }
}
.loading-parent{
  width:100%;
  height:100px;
  padding:1em;
  border:1px solid @gray-light;
  position: relative;
  margin-bottom:10px;
}
</style>



