<template>
  <div class="picker-render-view">
    <div class="select" @click="showPicker(0)" ref="select0">{{ selectedText[0] }}</div>
    <picker @select="handleSelect(0,arguments)"
      :selected-index="selectedIndex[0]"
      ref="picker0"
      :title="title[0]"
      cancelTxt=""
      confirmTxt=""></picker>
    <div class="select" @click="showPicker(1)" ref="select1">{{ selectedText[1] }}</div>
    <picker @select="handleSelect(1,arguments)"
      :data="data[1]"
      :selected-index="selectedIndex[1]"
      ref="picker1"
      :title="title[1]"
      cancelTxt=""
      confirmTxt=""></picker>

    <div class="select" @click="showPicker(2)" ref="select2">{{ selectedText[2] }}</div>
    <picker @select="handleSelect(2,arguments)"
      :data="data[2]"
      :selected-index="selectedIndex[2]"
      ref="picker2"
      :title="title[2]"
      cancelTxt=""
      confirmTxt=""></picker>
  </div>
</template>

<script type="text/ecmascript-6">
import Picker from '@/components/Picker.vue'

let data1 = [
  {
    text: '',
    value: 1
  },
  {
    text: '',
    value: 2
  },
  {
    text: '',
    value: 3
  },
  {
    text: '',
    value: 4
  },
  {
    text: '',
    value: 5
  },
  {
    text: '',
    value: 6
  },
  {
    text: '',
    value: 7
  },
  {
    text: '',
    value: 8
  },
  {
    text: '',
    value: 9
  },
  {
    text: '',
    value: 10
  },
  {
    text: '',
    value: 11
  },
  {
    text: '',
    value: 12
  },
  {
    text: '',
    value: 13
  },
  {
    text: '',
    value: 14
  },
  {
    text: '',
    value: 15
  }
]

let data2 = [
  {
    text: '',
    value: 'a'
  },
  {
    text: '',
    value: 'b'
  },
  {
    text: '',
    value: 'c'
  },
  {
    text: '',
    value: 'd'
  },
  {
    text: '',
    value: 'e'
  },
  {
    text: '',
    value: 'f'
  },
  {
    text: '',
    value: 'g'
  },
  {
    text: '',
    value: 'h'
  }
]

let data3 = [
  {
    text: '',
    value: 's'
  },
  {
    text: '',
    value: 'ss'
  },
  {
    text: '',
    value: 'sss'
  },
  {
    text: '',
    value: 'ssss'
  },
  {
    text: '',
    value: 'sssss'
  },
  {
    text: '',
    value: 'ssssss'
  }
]

export default {
  mounted () {
    this.$refs.picker0.setData([data1])
    this.$refs.picker0.setSelectedIndex([1])
  },
  data () {
    return {
      data: [
        [data1],
        [data1, data2],
        [data1, data2, data3]
      ],
      selectedIndex: [[0], [1, 0], [0, 1, 2]],
      selectedText: ['', '', ''],
      title: ['', '', '']
    }
  },
  methods: {
    showPicker (index) {
      let picker = this.$refs['picker' + index]
      picker.show()
    },
    handleSelect (index, args) {
      this.selectedText.splice(index, 1, args[2].join(''))
    }
  },
  components: {
    Picker
  }
}
</script>

<style scoped lang="less">
.picker-render-view{
  width:100%;
  height:100%;
  padding:15px;
  .select {
    margin: 0 10px 20px 10px;
    height: 40px;
    line-height: 40px;
    border: 1px solid #e5e5e5;
    text-align: center;
    background: #fff;
  }
}
</style>



